'use client';

import { useState, useMemo } from 'react';
import { useRouter } from 'next/navigation';
import {
  products,
  inventory,
  locations,
  getTotalQuantity,
  formatCurrency,
  formatCurrencyPrecise,
  formatNumber,
} from '@/data/mockData';
import { Product } from '@/types';

type CategoryFilter = 'all' | 'nutrient' | 'supply' | 'packaging' | 'label';
type StatusFilter = 'all' | 'in_stock' | 'low_stock' | 'out_of_stock';
type LocationFilter = 'all' | 'loc-1' | 'loc-2' | 'loc-3';

export default function InventoryPage() {
  const router = useRouter();
  const [searchQuery, setSearchQuery] = useState('');
  const [categoryFilter, setCategoryFilter] = useState<CategoryFilter>('all');
  const [statusFilter, setStatusFilter] = useState<StatusFilter>('all');
  const [locationFilter, setLocationFilter] = useState<LocationFilter>('all');
  const [selectedProducts, setSelectedProducts] = useState<Set<string>>(new Set());
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 20;

  // Get stock by location
  const getStockByLocation = (productId: string, locationId: string) => {
    const item = inventory.find(
      (i) => i.productId === productId && i.locationId === locationId
    );
    return item?.quantity || 0;
  };

  // Get stock status
  const getStockStatus = (product: Product) => {
    const total = getTotalQuantity(product.id);
    const reorderPoint = product.reorderPoint || 50;
    if (total === 0) return 'out_of_stock';
    if (total < reorderPoint) return 'low_stock';
    return 'in_stock';
  };

  // Calculate margin
  const calculateMargin = (msrp: number, cost: number) => {
    if (msrp === 0) return 0;
    return ((msrp - cost) / msrp) * 100;
  };

  // Filter products
  const filteredProducts = useMemo(() => {
    return products.filter((product) => {
      if (!product.isActive) return false;

      // Search filter
      if (searchQuery) {
        const query = searchQuery.toLowerCase();
        if (
          !product.name.toLowerCase().includes(query) &&
          !product.sku.toLowerCase().includes(query) &&
          !(product.barcode?.toLowerCase().includes(query))
        ) {
          return false;
        }
      }

      // Category filter
      if (categoryFilter !== 'all' && product.category !== categoryFilter) {
        return false;
      }

      // Status filter
      const status = getStockStatus(product);
      if (statusFilter !== 'all' && status !== statusFilter) {
        return false;
      }

      return true;
    });
  }, [searchQuery, categoryFilter, statusFilter]);

  // Pagination
  const totalPages = Math.ceil(filteredProducts.length / itemsPerPage);
  const paginatedProducts = filteredProducts.slice(
    (currentPage - 1) * itemsPerPage,
    currentPage * itemsPerPage
  );

  // Calculate stats
  const activeProducts = products.filter(p => p.isActive);
  const totalUnits = activeProducts.reduce((sum, p) => sum + getTotalQuantity(p.id), 0);
  const retailValue = activeProducts.reduce((sum, p) => sum + (getTotalQuantity(p.id) * p.prices.msrp), 0);
  const costOnHand = activeProducts.reduce((sum, p) => sum + (getTotalQuantity(p.id) * p.costs.base), 0);
  const lowStockCount = activeProducts.filter(p => {
    const qty = getTotalQuantity(p.id);
    const reorderPoint = p.reorderPoint || 50;
    return qty < reorderPoint && qty > 0;
  }).length;
  const outOfStockCount = activeProducts.filter(p => getTotalQuantity(p.id) === 0).length;

  // Category badge styles
  const getCategoryBadge = (category: Product['category']) => {
    const styles: Record<string, string> = {
      nutrient: 'bg-emerald-500/20 text-emerald-400',
      supply: 'bg-slate-500/20 text-slate-400',
      packaging: 'bg-purple-500/20 text-purple-400',
      label: 'bg-amber-500/20 text-amber-400',
    };
    return styles[category] || 'bg-slate-500/20 text-slate-400';
  };

  const getCategoryLabel = (category: Product['category']) => {
    const labels: Record<string, string> = {
      nutrient: 'Classic Line',
      supply: 'Shipping Supplies',
      packaging: 'Packaging',
      label: 'Labels',
    };
    return labels[category] || category;
  };

  // Handle row click
  const handleRowClick = (productId: string) => {
    router.push(`/inventory/${productId}`);
  };

  // Handle checkbox
  const handleCheckbox = (productId: string, e: React.MouseEvent) => {
    e.stopPropagation();
    const newSelected = new Set(selectedProducts);
    if (newSelected.has(productId)) {
      newSelected.delete(productId);
    } else {
      newSelected.add(productId);
    }
    setSelectedProducts(newSelected);
  };

  // Handle select all
  const handleSelectAll = () => {
    if (selectedProducts.size === paginatedProducts.length) {
      setSelectedProducts(new Set());
    } else {
      setSelectedProducts(new Set(paginatedProducts.map(p => p.id)));
    }
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-white">Inventory</h1>
          <p className="text-slate-400 text-sm">Manage your products and stock levels</p>
        </div>
        <div className="flex items-center gap-3">
          <button className="flex items-center gap-2 px-4 py-2.5 bg-emerald-600 hover:bg-emerald-500 text-white rounded-lg transition-colors text-sm font-medium">
            <i className="fas fa-plus"></i>
            Add Product
          </button>
          <button className="flex items-center gap-2 px-4 py-2.5 bg-slate-800/50 backdrop-blur border border-slate-700/50 text-slate-300 hover:text-white hover:bg-slate-700/50 rounded-lg transition-colors text-sm">
            <i className="fas fa-upload"></i>
            Import
          </button>
          <button className="flex items-center gap-2 px-4 py-2.5 bg-slate-800/50 backdrop-blur border border-slate-700/50 text-slate-300 hover:text-white hover:bg-slate-700/50 rounded-lg transition-colors text-sm">
            <i className="fas fa-download"></i>
            Export
          </button>
        </div>
      </div>

      {/* Stats Row */}
      <div className="grid grid-cols-5 gap-4">
        {/* Total Products */}
        <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl p-5">
          <div className="flex items-center justify-between mb-3">
            <span className="text-slate-400 text-sm">Total Products</span>
            <div className="w-10 h-10 bg-emerald-500/20 rounded-lg flex items-center justify-center">
              <i className="fas fa-cubes text-emerald-400"></i>
            </div>
          </div>
          <p className="text-3xl font-bold text-white">{activeProducts.length}</p>
          <p className="text-xs text-slate-400 mt-1">Active SKUs</p>
        </div>

        {/* Total Units */}
        <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl p-5">
          <div className="flex items-center justify-between mb-3">
            <span className="text-slate-400 text-sm">Total Units</span>
            <div className="w-10 h-10 bg-blue-500/20 rounded-lg flex items-center justify-center">
              <i className="fas fa-layer-group text-blue-400"></i>
            </div>
          </div>
          <p className="text-3xl font-bold text-white">{formatNumber(totalUnits)}</p>
          <p className="text-xs text-emerald-400 mt-1">
            <i className="fas fa-arrow-up mr-1"></i>
            +12% this month
          </p>
        </div>

        {/* Retail Value */}
        <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl p-5">
          <div className="flex items-center justify-between mb-3">
            <span className="text-slate-400 text-sm">Retail Value</span>
            <div className="w-10 h-10 bg-purple-500/20 rounded-lg flex items-center justify-center">
              <i className="fas fa-dollar-sign text-purple-400"></i>
            </div>
          </div>
          <p className="text-3xl font-bold text-white">{formatCurrency(retailValue)}</p>
          <p className="text-xs text-slate-400 mt-1">At MSRP</p>
        </div>

        {/* Cost on Hand */}
        <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl p-5">
          <div className="flex items-center justify-between mb-3">
            <span className="text-slate-400 text-sm">Cost on Hand</span>
            <div className="w-10 h-10 bg-amber-500/20 rounded-lg flex items-center justify-center">
              <i className="fas fa-coins text-amber-400"></i>
            </div>
          </div>
          <p className="text-3xl font-bold text-white">{formatCurrency(costOnHand)}</p>
          <p className="text-xs text-slate-400 mt-1">
            {retailValue > 0 ? ((retailValue - costOnHand) / retailValue * 100).toFixed(1) : 0}% potential margin
          </p>
        </div>

        {/* Low Stock */}
        <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl p-5">
          <div className="flex items-center justify-between mb-3">
            <span className="text-slate-400 text-sm">Low Stock Items</span>
            <div className="w-10 h-10 bg-red-500/20 rounded-lg flex items-center justify-center">
              <i className="fas fa-exclamation-triangle text-red-400"></i>
            </div>
          </div>
          <p className="text-3xl font-bold text-amber-400">{lowStockCount}</p>
          <p className="text-xs text-red-400 mt-1">{outOfStockCount} out of stock</p>
        </div>
      </div>

      {/* Filters Row */}
      <div className="flex items-center justify-between gap-4">
        {/* Search */}
        <div className="relative">
          <input
            type="text"
            placeholder="Search by SKU, name, barcode..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className="bg-slate-800/50 border border-slate-600/50 rounded-lg pl-10 pr-4 py-2.5 text-sm text-white placeholder-slate-400 focus:outline-none focus:border-emerald-500/50 w-80"
          />
          <i className="fas fa-search absolute left-3 top-1/2 -translate-y-1/2 text-slate-400"></i>
        </div>

        {/* Filter Dropdowns */}
        <div className="flex items-center gap-3">
          {/* Category */}
          <select
            value={categoryFilter}
            onChange={(e) => setCategoryFilter(e.target.value as CategoryFilter)}
            className="bg-slate-800/50 border border-slate-600/50 rounded-lg px-4 py-2.5 text-sm text-white focus:outline-none focus:border-emerald-500/50"
          >
            <option value="all">All Categories</option>
            <option value="nutrient">Classic Line</option>
            <option value="supply">Shipping Supplies</option>
            <option value="packaging">Packaging</option>
            <option value="label">Labels</option>
          </select>

          {/* Location */}
          <select
            value={locationFilter}
            onChange={(e) => setLocationFilter(e.target.value as LocationFilter)}
            className="bg-slate-800/50 border border-slate-600/50 rounded-lg px-4 py-2.5 text-sm text-white focus:outline-none focus:border-emerald-500/50"
          >
            <option value="all">All Locations</option>
            <option value="loc-1">Cronk Warehouse</option>
            <option value="loc-2">Amazon USA</option>
            <option value="loc-3">Amazon Canada</option>
          </select>

          {/* Status */}
          <select
            value={statusFilter}
            onChange={(e) => setStatusFilter(e.target.value as StatusFilter)}
            className="bg-slate-800/50 border border-slate-600/50 rounded-lg px-4 py-2.5 text-sm text-white focus:outline-none focus:border-emerald-500/50"
          >
            <option value="all">All Status</option>
            <option value="in_stock">In Stock</option>
            <option value="low_stock">Low Stock</option>
            <option value="out_of_stock">Out of Stock</option>
          </select>
        </div>
      </div>

      {/* Inventory Table */}
      <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl overflow-hidden">
        <div className="overflow-x-auto">
          <table className="w-full">
            <thead>
              <tr className="text-left text-slate-400 text-sm border-b border-slate-700/50 bg-slate-800/30">
                <th className="p-4 font-medium w-12">
                  <input
                    type="checkbox"
                    checked={selectedProducts.size === paginatedProducts.length && paginatedProducts.length > 0}
                    onChange={handleSelectAll}
                    className="rounded border-slate-600 bg-slate-700 text-emerald-500 focus:ring-emerald-500/50"
                  />
                </th>
                <th className="p-4 font-medium">Product</th>
                <th className="p-4 font-medium">SKU</th>
                <th className="p-4 font-medium">Category</th>
                <th className="p-4 font-medium text-right">Cronk WH</th>
                <th className="p-4 font-medium text-right">Amazon USA</th>
                <th className="p-4 font-medium text-right">Amazon CA</th>
                <th className="p-4 font-medium text-right">Total</th>
                <th className="p-4 font-medium text-right">Cost</th>
                <th className="p-4 font-medium text-right">MSRP</th>
                <th className="p-4 font-medium text-right">Margin</th>
                <th className="p-4 font-medium">Status</th>
                <th className="p-4 font-medium w-20">Actions</th>
              </tr>
            </thead>
            <tbody className="text-sm">
              {paginatedProducts.map((product) => {
                const cronkWH = getStockByLocation(product.id, 'loc-1');
                const amazonUSA = getStockByLocation(product.id, 'loc-2');
                const amazonCA = getStockByLocation(product.id, 'loc-3');
                const total = getTotalQuantity(product.id);
                const status = getStockStatus(product);
                const margin = calculateMargin(product.prices.msrp, product.costs.base);

                return (
                  <tr
                    key={product.id}
                    onClick={() => handleRowClick(product.id)}
                    className="border-b border-slate-700/30 hover:bg-slate-800/30 transition-colors cursor-pointer"
                  >
                    <td className="p-4" onClick={(e) => handleCheckbox(product.id, e)}>
                      <input
                        type="checkbox"
                        checked={selectedProducts.has(product.id)}
                        onChange={() => {}}
                        className="rounded border-slate-600 bg-slate-700 text-emerald-500 focus:ring-emerald-500/50"
                      />
                    </td>
                    <td className="p-4">
                      <div className="flex items-center gap-3">
                        <div className="w-10 h-10 bg-slate-700 rounded-lg flex items-center justify-center">
                          <i className={`fas ${product.category === 'nutrient' ? 'fa-bottle-droplet' : product.category === 'label' ? 'fa-tag' : product.category === 'supply' ? 'fa-box' : 'fa-cube'} text-slate-400`}></i>
                        </div>
                        <div>
                          <p className="font-medium text-white">{product.name}</p>
                          <p className="text-xs text-slate-400">{product.barcode || ''}</p>
                        </div>
                      </div>
                    </td>
                    <td className="p-4 text-slate-300 font-mono text-sm">{product.sku}</td>
                    <td className="p-4">
                      <span className={`px-2 py-1 ${getCategoryBadge(product.category)} rounded text-xs`}>
                        {getCategoryLabel(product.category)}
                      </span>
                    </td>
                    <td className="p-4 text-right text-white">{formatNumber(cronkWH)}</td>
                    <td className="p-4 text-right text-slate-300">{formatNumber(amazonUSA)}</td>
                    <td className="p-4 text-right text-slate-300">{formatNumber(amazonCA)}</td>
                    <td className="p-4 text-right text-white font-medium">{formatNumber(total)}</td>
                    <td className="p-4 text-right text-slate-300">{formatCurrencyPrecise(product.costs.base)}</td>
                    <td className="p-4 text-right text-white">
                      {product.prices.msrp > 0 ? formatCurrencyPrecise(product.prices.msrp) : ''}
                    </td>
                    <td className="p-4 text-right">
                      {product.prices.msrp > 0 ? (
                        <span className={margin >= 50 ? 'text-emerald-400' : margin >= 30 ? 'text-amber-400' : 'text-red-400'}>
                          {margin.toFixed(1)}%
                        </span>
                      ) : (
                        <span className="text-slate-500"></span>
                      )}
                    </td>
                    <td className="p-4">
                      {status === 'in_stock' && (
                        <span className="flex items-center gap-1.5 text-emerald-400 text-sm">
                          <span className="w-1.5 h-1.5 bg-emerald-400 rounded-full"></span>
                          In Stock
                        </span>
                      )}
                      {status === 'low_stock' && (
                        <span className="flex items-center gap-1.5 text-amber-400 text-sm">
                          <span className="w-1.5 h-1.5 bg-amber-400 rounded-full"></span>
                          Low Stock
                        </span>
                      )}
                      {status === 'out_of_stock' && (
                        <span className="flex items-center gap-1.5 text-red-400 text-sm">
                          <span className="w-1.5 h-1.5 bg-red-400 rounded-full"></span>
                          Out of Stock
                        </span>
                      )}
                    </td>
                    <td className="p-4" onClick={(e) => e.stopPropagation()}>
                      <div className="flex items-center gap-1">
                        <button
                          onClick={() => handleRowClick(product.id)}
                          className="p-2 hover:bg-slate-700/50 rounded-lg transition-all"
                          title="View"
                        >
                          <i className="fas fa-eye text-slate-400 hover:text-white"></i>
                        </button>
                        <button
                          className="p-2 hover:bg-slate-700/50 rounded-lg transition-all"
                          title="Edit"
                        >
                          <i className="fas fa-edit text-slate-400 hover:text-white"></i>
                        </button>
                        <button
                          className="p-2 hover:bg-slate-700/50 rounded-lg transition-all"
                          title="Adjust Stock"
                        >
                          <i className="fas fa-sliders text-slate-400 hover:text-white"></i>
                        </button>
                      </div>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>

        {/* Empty State */}
        {filteredProducts.length === 0 && (
          <div className="py-12 text-center">
            <i className="fas fa-box-open text-4xl text-slate-600 mb-4"></i>
            <p className="text-sm text-slate-400">No products found</p>
          </div>
        )}

        {/* Pagination */}
        {filteredProducts.length > 0 && (
          <div className="flex items-center justify-between p-4 border-t border-slate-700/50">
            <p className="text-sm text-slate-400">
              Showing {((currentPage - 1) * itemsPerPage) + 1}-{Math.min(currentPage * itemsPerPage, filteredProducts.length)} of {filteredProducts.length} products
            </p>
            <div className="flex items-center gap-2">
              <button
                onClick={() => setCurrentPage(p => Math.max(1, p - 1))}
                disabled={currentPage === 1}
                className="px-3 py-1.5 bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-lg text-slate-400 hover:text-white transition-all disabled:opacity-50 disabled:cursor-not-allowed"
              >
                <i className="fas fa-chevron-left"></i>
              </button>

              {Array.from({ length: Math.min(totalPages, 5) }, (_, i) => {
                let pageNum;
                if (totalPages <= 5) {
                  pageNum = i + 1;
                } else if (currentPage <= 3) {
                  pageNum = i + 1;
                } else if (currentPage >= totalPages - 2) {
                  pageNum = totalPages - 4 + i;
                } else {
                  pageNum = currentPage - 2 + i;
                }

                return (
                  <button
                    key={pageNum}
                    onClick={() => setCurrentPage(pageNum)}
                    className={`px-3 py-1.5 rounded-lg transition-all ${
                      currentPage === pageNum
                        ? 'bg-emerald-500/20 text-emerald-300'
                        : 'bg-slate-800/50 backdrop-blur border border-slate-700/50 text-slate-400 hover:text-white'
                    }`}
                  >
                    {pageNum}
                  </button>
                );
              })}

              {totalPages > 5 && currentPage < totalPages - 2 && (
                <>
                  <span className="text-slate-500">...</span>
                  <button
                    onClick={() => setCurrentPage(totalPages)}
                    className="px-3 py-1.5 bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-lg text-slate-400 hover:text-white transition-all"
                  >
                    {totalPages}
                  </button>
                </>
              )}

              <button
                onClick={() => setCurrentPage(p => Math.min(totalPages, p + 1))}
                disabled={currentPage === totalPages}
                className="px-3 py-1.5 bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-lg text-slate-400 hover:text-white transition-all disabled:opacity-50 disabled:cursor-not-allowed"
              >
                <i className="fas fa-chevron-right"></i>
              </button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}
